<template>
  <div class="index">
  	<!--轮播图-->
  	<div class="play">
		  <!-- Swiper -->
		  <div class="swiper-container">
		    <div class="swiper-wrapper">
		      <div class="swiper-slide"><img src="../../static/images/shouhu03.jpg" alt=""></div>
		      <div class="swiper-slide"><img src="../../static/images/shouhu02.jpg" alt=""></div>
		      <div class="swiper-slide"><img src="../../static/images/shouhu01.jpg" alt=""></div>
		      <div class="swiper-slide"><img src="../../static/images/shouhu04.jpg" alt=""></div>
		      <div class="swiper-slide"><img src="../../static/images/shouhu05.jpg" alt=""></div>
		      <div class="swiper-slide"><img src="../../static/images/shouhu06.jpg" alt=""></div>
		    </div>
		    <!-- Add Pagination -->
		    <div class="swiper-pagination"></div>
		  </div>			
		</div>
		<!--滑动图片-->
		<div id="picScroll" class="picScroll">
				<div class="hd">
						<span class="next"></span>
						<ul></ul>
						<span class="prev"></span>
						<h3>宫崎骏作品集</h3>
				</div>
				<div class="bd">
						<ul>
							<li><a href="#"><img _src="../../static/images/gong02.jpg" src="images/blank.png" /></a></li>
							<li><a href="#"><img _src="../../static/images/gong01.jpg" src="images/blank.png" /></a></li>
							<li><a href="#"><img _src="../../static/images/gong03.jpg" src="images/blank.png"/></a></li>
						</ul>

						<ul>
							<li><a href="#"><img _src="../../static/images/gong04.jpg" src="images/blank.png" /></a></li>
							<li><a href="#"><img _src="../../static/images/gong05.jpg" src="images/blank.png" /></a></li>
							<li><a href="#"><img _src="../../static/images/gong06.jpg" src="images/blank.png" /></a></li>
						</ul>

						<ul>
							<li><a href="#"><img _src="../../static/images/gong07.jpg" src="images/blank.png" /></a></li>
							<li><a href="#"><img _src="../../static/images/gong08.jpg" src="images/blank.png" /></a></li>
							<li><a href="#"><img _src="../../static/images/gong09.jpg" src="images/blank.png" /></a></li>
						</ul>

				</div>
			</div>
			<!--作品介绍-->
			<div class="story">
				<div class="zuopin" v-for="sto in datas">
					<div class="title">{{sto.name}}</div>
					<p class="con">{{sto.content}}</p>
				</div>
			</div>
			

  </div>
</template>

<script>
//引入swiper 组件
import Swiper from 'swiper';
//引入swiper css 的文件
import 'swiper/dist/css/swiper.min.css'
export default {
  name: 'Index',
  data () {
    return {
      datas:[
      	{"id":"1","name":'龙猫',"content":'龙猫是1988年由宫崎骏执导的动画电影，描述了在日本经济高度发展前的美丽自然，只有孩子才能看见的世界及丰富的想象。'},
      	{"id":"2","name":'千与千寻',"content":'千与千寻，该片由宫崎骏执导，讲述了少女千寻意外来到了神灵异世界后发生的故事。该片或2003年，奥斯卡金像奖最佳长篇动画奖。'},
      	{"id":"3","name":'天空之城',"content":'天空之城，由宫崎骏执导，故事讲述由希达坐的军队飞行船遭到空中海盗的袭击开始的。争斗中希达从万米高空的飞行船上跌落下来。'},
      	{"id":"4","name":'魔女宅急便',"content":'由宫崎骏执导的动画电影，讲述魔发少女琪琪离家独立修行的经历，描述一般青春期女孩在希望独立和自力更生所面临的一些困境。'},
      	{"id":"5","name":'哈尔的移动城堡',"content":'由宫崎骏执导，电影描述苏菲是制帽专家，遭到巫女嫉恨，把她由18的少女变成90岁老太。'},
      	{"id":"6","name":'幽灵公主',"content":'讲述人类对大自然的破坏，反映人类和自然是否能真正实现和谐共处。'},
      	{"id":"7","name":'风之谷',"content":'由宫崎骏执导，讲述人类文明巅峰后由战争毁于一旦，世界变菌类构成被新生态所掩盖，人类生活在很少部分土地上，面对巨型昆虫和会释放瘴气的森林威胁和包围，努力求生存的故事。'},
      	{"id":"8","name":'梅与小猫巴士',"content":'讲述了梅和小猫巴士的故事。'},
      	{"id":"9","name":'种下星星的日子',"content":'影片讲述了小男孩NONA种下一个星星后发生的奇妙故事。'},
      	{"id":"3","name":'红猪',"content":'由宫崎骏执导，影片讲述了自己被诅咒变成猪的主人公，打击空中劫匪，保护身边的人的故事'},
    
      	
      ]
    }
  },
  mounted:function(){
  	var swiper = new Swiper('.swiper-container', {
      pagination: {
        el: '.swiper-pagination',
        clickable :true,
      },
    }); 
    TouchSlide({ 
					slideCell:"#picScroll",
					titCell:".hd ul", //开启自动分页 autoPage:true ，此时设置 titCell 为导航元素包裹层
					autoPage:true, //自动分页
					pnLoop:"false", // 前后按钮不循环
					switchLoad:"_src" //切换加载，真实图片路径为"_src" 
				});

  }
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
	// 引入基础的样式
  @import '../assets/sass/base.scss';
  .index{
  	width:rem(710px);
  	margin: rem(100px) auto;
  	.play{
  		img{
  			width:100%;
  			height:rem(500px);
  		}
  		/deep/ .swiper-pagination-bullet{
				width: rem(20px);
				height: rem(20px);
				
			}
  	}
  	#picScroll {
  		width:rem(710px);
  		margin-top:rem(20px);
  		.bd{
  			width:100%;
  			height:rem(400px);
  			ul{
  				float:left;
  				width:100%;
  				padding-top:10px;
  				li{
  					width:rem(710px)/3;
  					float:left;
  					text-align: center;
  					img{
  						width:90%;
  						height:rem(300px);
  					}
  				}
  			}
  			
  		}
  		.hd{
  			
  			height:rem(60px);
  			line-height: rem(60px);
  			border-bottom:rem(2px) solid #80BD6D; 
  			background:#f6f6f6;   
  			overflow:hidden; 
  			text-align:left;  
  			padding:0 rem(10px);
  			.on{
  				 background:#80BD6D; 
  			}
  			
  			ul{
  				float: right;
  				padding-top:rem(24px); 
  				li{
  					float:left; 
  					width:rem(8px); 
  					height:rem(8px); 
  					background:#D0D0D0; 
  					margin:0 rem(5px); 
  					overflow:hidden; 
						-webkit-border-radius:rem(8px); 
						-moz-border-radius:rem(8px); 
						border-radius:rem(8px); 

  				}
  			}
  		}
  		
  	}
  	.story{
  		width:100%;
  		
  		margin-bottom:rem(150px);
  		.zuopin{
					line-height:rem(60px);
					font-size:rem(36px);
					border-bottom:rem(3px) dashed #999;
					margin-bottom:rem(20px);
					
  			.title{
	  			float: left;
	  			margin-right:rem(40px);
	  			color:palevioletred;
	  			font-weight: bold;
	  		}
	  		.con{
	  			color:#666;
	  			overflow: hidden;
	  			white-space: nowrap;
	  			text-overflow: ellipsis;
	  		}
  		}
  		
  	}
  }
  


</style>



