<template>
  <div id="app">
    <!--顶部logo-->
    <div class="banner">
    	<span class="iconfont icon-logo-octocat"></span>
    </div>
    <router-view class="container"></router-view>
    
    
    <nav class="bottom">
    	<ul>
    		<router-link to="/index" tag="li" :class='resPath=="index"?"xz":""'>
    			<span class="iconfont icon-homepage"></span>
    			<p>首页</p>
    		</router-link>
    		<router-link to="/luntan" tag="li" :class='resPath=="luntan"?"xz":""'>
    			<span class="iconfont icon-interactive"></span>
    			<p>论坛</p>
    		</router-link>
    		<router-link to="/position" tag="li" :class='resPath=="position"?"xz":""'>
    			<span class="iconfont icon-label"></span>
    			<p>位置</p>
    		</router-link>
    		<router-link to="/we" tag="li" :class='resPath=="we"?"xz":""'>
    			<span class="iconfont icon-group"></span>
    			<p>我们</p>
    		</router-link>
    	</ul>
    </nav>
  </div>
</template>

<script>
export default {
  name: 'App',
  computed:{
  	//路径
  	resPath:function(){
  		var arr=this.$route.path.split('/');
  		return arr[1];
  		console.log(arr[1]);
  	}
  }
}
</script>

<style lang="scss">
	// 引入基础的样式
  @import './assets/sass/base.scss';
  
	.banner{
		position: fixed;
		top:0;
		left:0;
		z-index: 99;
		width:100%;
		height:rem(90px);
		background:#333;
		text-align: center;
		line-height: rem(90px);
		span{
			font-size:rem(60px);
			color:yellow;
		}
	}
	nav.bottom{
		position: fixed;
		bottom:0;
		left:0;
		width:100%;
		height:rem(100px);
		background:#333;
		ul{
			display:flex;
			align-items: center;
			height:rem(100px);
			li{
				width:25%;
				height:rem(100px);
				text-align: center;
				span{
					font-size:rem(50px);
					color:#fff;
				}
				p{
					font-size: rem(30px);
					color:#fff;
				}
				&.xz{
					span{
						color:yellow;
					}
					p{
						color:yellow;
					}
				}
				
			}
			
		}
	}
</style>



