<template>
  <div class="position" id="allmap">
  
    </div>
</template>

<script>
import bikeLogo from '../../static/images/bike.jpg';
export default {
  name: 'Position',
  data () {
    return {
      
    }
  },
  methods:{
  	 addMarker:function(map,point, index){  // 创建图标对象   
			var myIcon = new BMap.Icon(bikeLogo, new BMap.Size(46, 50), {    
			   anchor: new BMap.Size(10, 25),    
			 });      
			// 创建标注对象并添加到地图   
			 var marker = new BMap.Marker(point, {icon: myIcon});    
			 map.addOverlay(marker);    
			}    
  },
  mounted:function(){
  	//初始化地图
	var map = new BMap.Map("allmap");
	var point = new BMap.Point(116.331398,39.897445);
	
//定位
	var self=this;
	var geolocation = new BMap.Geolocation();
	geolocation.getCurrentPosition(function(r){
		if(this.getStatus() == BMAP_STATUS_SUCCESS){
			point = new BMap.Point(r.point.lng,r.point.lat)
//			alert('您的位置：'+r.point.lng+','+r.point.lat);
		}
		else {
			alert('failed'+this.getStatus());
		}   
		map.centerAndZoom(point,12);
		
		
//		//获取数据
//		fetch(`/api/broken-bikes?lat=110&lng=23`).then((data)=>{
//			console.log(data);
//			return data.json();
//		})
//		.then((json)=>{
//			for(let i=0;i<json.length;i++){
//				let point=new BMap.Point(json[i].lng,json[i].lat);
//				self.addMarker(map,point);
//			}
//		})
		self.addMarker(map,point);
	},{enableHighAccuracy: true})
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
	// 引入基础的样式
  @import '../assets/sass/base.scss';
  #allmap{
  	margin-top:rem(90px);
  	margin-bottom:rem(100px);
  	height: rem(2000px);
  }
  .BMap_Marker img{
  	width:100%;
  }
  
</style>



