<template>
  <div class="luntan">
  	<nav class="top">
  		<ul>
  			<router-link to="/luntan/" tag="li">全部</router-link>
  			<router-link to="/luntan/good" tag="li">精华</router-link>
  			<router-link to="/luntan/share" tag="li">分享</router-link>
  			<router-link to="/luntan/ask" tag="li">问答</router-link>
  			<router-link to="/luntan/star" tag="li">星座运势</router-link>	
  		</ul>	
  	</nav>
  	<router-view></router-view>
    </div>
</template>

<script>
export default {
  name: 'Luntan',
  data () {
    return {
      
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
	// 引入基础的样式
  @import '../assets/sass/base.scss';
  h1{
  	margin-top:rem(90px);
  }
  .top{
  	position:fixed;
  	top:rem(90px);
  	left:0;
  	width:100%;
  	height:rem(80px);
  	ul{
  		display:flex;
  		align-items: center;
  		justify-content: space-around;
  		height:rem(80px);
  		background: #aaa;
  		li{
  			font-size:rem(35px);
  			padding:0 rem(10px);
  			background:#fff;
  			border-radius: rem(8px);
  			color:#333;
  			&.router-link-exact-active{
  				background:#444;
  				color:#fff;
  			}
  		}
  	}
  }
  
</style>



