import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import Luntan from '@/components/Luntan'
import Position from '@/components/Position'
import We from '@/components/We'

//子路由
import All from '@/components/topics/All'
import Good from '@/components/topics/Good'
import Share from '@/components/topics/Share'
import Ask from '@/components/topics/Ask'
import Star from '@/components/topics/Star'
import Show from '@/components/topics/Show'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/index',
      name: 'Index',
      component: Index
    },
    {
    	path:'/luntan',
    	name:'Luntan',
    	component:Luntan,
    	children:[
    	{
    		path:'/',
    		name:'All',
    		component:All
    	},
    	{
    		path:'good',
    		name:'Good',
    		component:Good
    	},
    	{
    		path:'share',
    		name:'Share',
    		component:Share
    	},
    	{
    		path:'ask',
    		name:'Ask',
    		component:Ask
    	},
    	{
    		path:'star',
    		name:'Star',
    		component:Star
    	},
    	 // 内容页
        {
          path:'show/:id',
          name: 'Show',
          component:Show 
        }
    	]
    },
    {
    	path:'/position',
    	name:'Position',
    	component:Position
    },
    {
    	path:'/we',
    	name:'We',
    	component:We
    },
    // 路由跳转
    {
      path:'/*',
      redirect:'/index'
    }
  ]
})



